import React from "react";
import Auxillary from "../../../HOC/Auxillary/Auxillary";
import Button from "../../UI/Button/Button";
import classes from "./OrderSummary.css";

const OrderSummary = (props) => {
  const ingredientSummary = Object.keys(props.ingredients).map((ikey) => {
    return (
      <li key={ikey}>
        {ikey} : {props.ingredients[ikey]}
      </li>
    );
  });
  return (
    <Auxillary>
      <h3>Your Order Summary</h3>
      <p>Burger with following ingrdients :</p>
      <ul>{ingredientSummary}</ul>
      <p>
        <strong>Total Price : {props.price}</strong>
      </p>
      <p className={classes.checkout}>Want to CheckOut?</p>
      <Button clicked={props.orderCanceled} typeBtn={"Danger"}>
        Cancel
      </Button>
      <Button clicked={props.orderedContinue} typeBtn={"Success"}>
        Continue
      </Button>
    </Auxillary>
  );
};

export default OrderSummary;
