import React from "react";
import classes from "./SideDrawer.css";
import Logo from "../../Logo/Logo";
import NavigationItems from "../NavigationItems/NavigationItems";
import Auxillary from "../../../HOC/Auxillary/Auxillary";
import BackDrop from "../../UI/BackDrop/BackDrop";

const SideDrawer = (props) => {
  let sideClasses = [classes.SideDrawer, classes.Close];
  if (props.open) {
    sideClasses = [classes.SideDrawer, classes.Open];
  }
  return (
    <Auxillary>
      <BackDrop close={props.close} show={props.open} />
      <div className={sideClasses.join(" ")}>
        <div className={classes.Logo}>
          <Logo close={props.close} />
        </div>
        <nav>
          <NavigationItems close={props.close} />
        </nav>
      </div>
    </Auxillary>
  );
};

export default SideDrawer;
