import React, { Component } from 'react';
import Auxillary from '../Auxillary/Auxillary';
import claases from './Layout.css';
import Toolbar from '../../Components/Navigation/Toolbar/Toolbar';
import SideDrawer from '../../Components/Navigation/SideDrawer/SideDrawer';

class Layout extends Component  {
    state ={
        sideDrawerVisible : false
    }

    sideDrawerHandler = () => {
        this.setState({sideDrawerVisible: false})
    }

    toggleHandler = () => {
        this.setState((prevState) => {
            return {sideDrawerVisible: !prevState.sideDrawerVisible}
        })
    }

    render(){
        return(
            <Auxillary>
                <Toolbar open = {this.toggleHandler}/>
                <SideDrawer close = {this.sideDrawerHandler}
                open = {this.state.sideDrawerVisible}/>
                <main className = {claases.Layout}>
                    {this.props.children}
                </main>
            </Auxillary>
        )
    }
};

export default Layout;