import React, { Component } from 'react';
import Auxillary from '../../HOC/Auxillary/Auxillary';
import Burger from '../../Components/Burger/Burger';
import BuildControls from '../../Components/Burger/BuildControls/BuildControls';
import Modal from '../../Components/UI/Modal/Modal';
import OrderSummary from '../../Components/Burger/OrderSummary/OrderSummary';
import axios from '../../axios-order';
import Spinner from '../../Components/UI/Spinner/Spinner';
import withErrorHandler from '../../HOC/withErrorHandler/withErrorHandler';

const Ingredients_Price = {
    Bacon:0.7,
    Cheese: 0.3,
    Meat: 1.2,
    Salad: 0.4
}

class BurgerBuilder extends Component {

    state = {
        ingredients : null,
        totalPrice: 4,
        CanPurchase: false,
        Ordered: false,
        loading: false,
        error: false
        
    }

    componentDidMount () {
        axios.get( 'https://react-burger-1111.firebaseio.com/ingredients.json' )
            .then( response => {
                this.setState( { ingredients: response.data } );
            } )
            .catch( error => {
                this.setState( { error: true } );
            } );
    }

    CanPurchaseHandler = (ingredient) => {
        const sum = Object.keys(ingredient).map(key => {
            return ingredient[key];
        }).reduce((sum, el) => {
            return sum + el;
        })
        this.setState({CanPurchase: sum > 0})
    }

    addIngredientHnadler = (type) => {
        const updatedCount = this.state.ingredients[type] + 1;
        const updatedIngredient = {...this.state.ingredients};
        updatedIngredient[type] = updatedCount;
        const updatedPrice = this.state.totalPrice + Ingredients_Price[type];
        this.setState({totalPrice: updatedPrice, ingredients: updatedIngredient});
        this.CanPurchaseHandler(updatedIngredient);
    }

    removeIngredientHandler = (type) => {
        if (this.state.ingredients[type] <=0 ) {
            return;
        }
        const updatedCount = this.state.ingredients[type] - 1;
        const updatedIngredient = {...this.state.ingredients};
        updatedIngredient[type] = updatedCount;
        const updatedPrice = this.state.totalPrice - Ingredients_Price[type];
        this.setState({totalPrice: updatedPrice, ingredients: updatedIngredient});
        this.CanPurchaseHandler(updatedIngredient);
    }

    orderedHandler = () => {
        this.setState({Ordered: true})
    }

    orderCanceled = () => {
        this.setState({Ordered: false})
    }

    orderedContinue = () => {
        //alert('Order Continue!!')
        // this.setState({loading: true});
        // const order = {
        //     ingredients: this.state.ingredients,
        //     price: this.state.totalPrice.toFixed(2),
        //     customer: {
        //         name: 'Anurag',
        //         email: 'sdfs@edrt.com'
        //     },
        //     deliveryMethod: 'fastest'
        // }
        // axios.post('/orders.json', order)
        // .then(res => this.setState({loading: false, Ordered: false}))
        // .catch(error => this.setState({loading: false, Ordered: false}))
        
        const queryParams = [];
        for (let i in this.state.ingredients) {
            queryParams.push(encodeURIComponent(i) + '=' + encodeURIComponent(this.state.ingredients[i]));
        }
        queryParams.push('totalPrice='+ this.state.totalPrice)
        const queryString = queryParams.join('&');
        this.props.history.push({
            pathname: '/checkout',
            search: '?' + queryString
        });
    }

    render() {

        const disableIngre = {...this.state.ingredients};
        for (let key in disableIngre) {
            disableIngre[key] = disableIngre[key] <= 0;
        }
        let orderSummary = null;
        let burger = this.state.error ? <p>Ingredients can't be loaded!</p> : <Spinner />;

        if ( this.state.ingredients ) {
            burger = (
                <Auxillary>
                    <Burger ingredient = {this.state.ingredients}></Burger>
                    <BuildControls 
                        add = {this.addIngredientHnadler}
                        remove = {this.removeIngredientHandler}
                        disabled = {disableIngre}
                        price = {this.state.totalPrice}
                        purchasable = {this.state.CanPurchase}
                        ordered = {this.orderedHandler}/>
                </Auxillary>
            );
            orderSummary = <OrderSummary 
            ingredients = {this.state.ingredients}
            orderCanceled = {this.orderCanceled}
            orderedContinue = {this.orderedContinue}
            price = {this.state.totalPrice.toFixed(2)} />
        }
        if ( this.state.loading ) {
            orderSummary = <Spinner />;
        }
        return (
            <Auxillary>
                <Modal show = {this.state.Ordered} clicked = {this.orderCanceled}>
                    {orderSummary}
                </Modal>
                {burger}
            </Auxillary>
        );
    }
}

export default withErrorHandler(BurgerBuilder, axios);